body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
/*     font-family: 'Asap', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3 {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
.App {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}
.sidebarTopBar {
    position: fixed;
    z-index: 98;
    width: 100%;
    height: 60px;
    margin: 0 auto;
    padding: 0 10px;
    background-color: white;
    border-bottom: 1px solid lightgray;
}

.sidebarOpener {
    display: inline-block;
    margin-left: 5px;
    padding: 15px;
    cursor: pointer;
    color: #666;
}

.sidebarTopBarLogo {
    display: inline-block;
    height: 55px;
    position: absolute;
    top: 2px;
    right: 30px;
}

.sidebarBackground {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(80, 80, 80, 0.7);
    display: none;
    z-index: 99;
}

.sidebarBgVisible {
    display: block;
}

.sidebar {
    position: fixed;
    background-color: white;
    width: 250px;
    height: 100%;
    transition: left 0.3s ease;
    z-index: 100;
}

.sidebarOpen {
    left: 0px;
}

.sidebarClosed {
    left: -250px;
}

.sidebarHeader {
    display: flex;
    height: 60px;
}

.sidebarLangSwitch {
    font-size: 16px;
    font-weight: 600;
    margin: 10px 0 10px 20px;
    padding: 10px;
    cursor: pointer;
    background-color: #08415c;
    color: white;
    border-radius: 20px;
}

.sidebarLangSwitch h4 {
    margin: 0;
    padding: 0;
}

.sidebarCloser {
    position: absolute;
    padding: 15px;
    margin-left: 180px;
    cursor: pointer;
}

.sidebarContentWrapper {
    overflow-y: auto;
    height: 100%;
}

.sidebarContent {
    margin: 50px 0 100px 20px;
}

.sidebarLink {
    text-decoration: none;
}

.sidebarMainLink {
    color: #333;
}

.sidebarSubLinkContainer {
    margin-left: 20px;
}

.sidebarSubLink {
    color: #666;
    margin: 10px 15px 0 0;
    padding: 10px;
    border-left: 10px solid #efc51f;
    border-bottom: 1px solid #efc51f;
}
.topNavBar {
    background-color: white;
    position: fixed;
    height: 60px;
    width: 100%;
    z-index: 100;
    border-bottom: 1px solid white;
}

.navElementWrapper {
    display: flex;
    align-items: center;
    height: 60px;
    width: 95%;
    margin: 0 auto;
    border-bottom: 1px solid lightgray;
}

.navLogo {
    height: 55px;
}

.navElementContainer {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 0 120px 0 20px;
    display: grid;
    grid-auto-flow: column;
    align-content: center;
    justify-items: center;
}

.langSelector {
    min-width: 80px;
    cursor: pointer;
    color: rgb(131, 131, 131);
    font-size: 0.9rem;
}

.langSelector:hover {
