footer {
  background-color: #08415c;
  min-height: 300px;
  padding: 30px 0 0 0;
  color: #eee;
  box-shadow: 0px -2px 2px #08415c;
}

.ftSitemap {
  width: 90%;
  max-width: 1000px;
  margin: 0 auto 50px auto;
  padding: 20px 0 40px 0;
  display: grid;
  grid-auto-flow: column;
  justify-items: center;
  justify-content: center;
  grid-column-gap: 200px;
  -webkit-column-gap: 200px;
          column-gap: 200px;
  text-align: center;
  border-bottom: 1px solid #aaa;
}

.ftSitemap h4 {
  font-size: 24px;
  margin: 15px 0;
}

.ftSitemapLink {
  display: block;
  font-size: 16px;
  font-weight: 600;
  margin: 10px 0;
  color: #bbb;
  text-decoration: none;
}

.ftSitemapLink:hover {
  color: black;
}

.ftContacts {
  max-width: 1000px;
  margin: 0 auto;
  padding: 30px 0 0 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(180px, 1fr));
  justify-items: center;
  align-items: center;
}

.ftPerson {
  text-align: center;
}

.ftPerson h4 {
  font-size: 20px;
  font-weight: 600;
  margin: 5px 0 10px 0;
}

.ftPerson h5 {
  font-size: 16px;
  font-weight: 600;
  margin: 5px 0;
}

.ftLocation {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.75;
  max-width: 180px;
}

.ftMoreContacts {
  color: #ddd;
}

.ftMoreContacts h2 {
  margin: 0;
  font-size: 20px;
}

.ftLogos {
  display: grid;
  max-width: 700px;
  padding: 0 40px;
  margin: 100px auto;
  grid-auto-flow: column;
  align-items: center;
}

.ftDTHLogo {
  display: block;
  width: 250px;
  justify-self: left;
}

.ftHelLogo {
  display: block;
  width: 250px;
  justify-self: right;
}

@media only screen and (max-width: 600px){
  .ftSitemap {
    grid-auto-flow: row;
  }
  .ftPerson:first-of-type {
    grid-row: 1;
    margin-bottom: 30px;
  }
  .ftPerson:last-of-type {
    grid-row: 2;
  }
  .ftContacts {
    grid-template-columns: 1fr;
  }
  .ftLocation {
    margin-top: 50px;
    grid-row: 3;
  }
  .ftLogos {
    grid-auto-flow: row;
    margin: 50px auto;
  }
  .ftDTHLogo, .ftHelLogo {
    justify-self: center;
    margin: 50px 0;
  }
}
.home {
    width: 100%;
}


.landing {
    height: 100vh;
    position: relative;
    text-align: center;
}

.landingBg {
    position: absolute;
    width: 100%;
    height: 98vh;
    background-image: url(/website/static/media/landingbg.e0e23d32.jpg);
    background-size: cover;
    background-position-x: 75%;
    background-attachment: fixed;
    background-repeat: no-repeat;
}

.landingContent {
    padding: 80px 0 30px 0;   
    width: 100%;
    max-height: 370px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #fff;
}

.landingLogo {
    width: 550px;
}

.sloganContainer {
    border-bottom: 2px solid #08415c;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    max-width: 500px;
    padding: 0 40px;
    margin: 20px auto 25px auto;
}

.sloganContainer h1 {
    font-size: 32px;
    font-weight: 300;
    font-style: italic;
    color: #08415c;
    background-color: white;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 0 10px;
    margin: 0 auto -20px auto;
}

.scrollIndicator {
    position: -webkit-sticky;
    position: sticky;
    bottom: 50px;
    display: block;
    margin: 20px auto;
    color: #efc51f;
    text-align: center;
    transition: opacity 0.5s;
}

.scrollText {
    margin-top: 10px;
    font-size: 20px;
    transition: opacity 0.4s;
    color: rgb(200, 140, 0);
}

.scrollTextVisible {
    opacity: 1;
}

.scrollTextHidden {
    opacity: 0;
}

.scrollIndHidden {
    opacity: 0;
}

@media only screen and (max-width: 600px){
    .landingContent {
        padding: 100px 0 30px 0;
    }
    .landingLogo {
        width: 350px;
    }
    .sloganContainer {
        padding: 0 20px;
        border-bottom-width: 1px;
    }
    .sloganContainer h1 {
        font-size: 18px;
        margin-bottom: -10px;
    }
    .landingContent h4 {
        margin-top: 10px;
        font-size: 14px;
        font-weight: 600;
    }
}
