    }
}
.people {
    position: relative;
    padding: 50px 0 10px 0;
}

.peopleAnchor {
    position: absolute;
    top: -60px;
}

.peopleTitle {
    text-align: center;
    width: 300px;
    margin: 0 auto;
    font-size: 36px;
    padding-bottom: 5px;
    border-bottom: 1px solid lightgrey;
}

.peopleGrid {
    max-width: 1000px;
    margin: 0 auto;
    background-color: white;
    padding: 20px;
}
.person {
    display: grid;
    grid-template-columns: 300px 270px 360px;
    justify-items: center;
    justify-content: center;
    border-bottom: 1px solid grey;
    margin: 10px 0;
    padding: 50px 0;
}

.person:last-of-type {
    border-bottom: none;
}

.personText {
    text-align: start;
    min-width: 250px;
    margin: 40px 0 0 0px;
    background-color: rgba(255, 255, 255, 0.5);
}

.personText i {
    margin-right: 5px;
}

.personText .fa-mobile-alt {
    margin-right: 12px;
}

.personText h3 {
    font-size: 28px;
    margin: 0 0 20px 0;
}

.personText h4 {
    font-size: 18px;
    margin: 20px 0;
}

.personEmail {
    text-decoration: none;
    color: #08415c;
}

.personEmail:hover {
    color: #4d64c1;
}

.personImg {
    width: 250px;
    height: 300px;
    margin: 10px 0;
    background-color: #ddd;
}

.personImg i {
    color: #aaa;
    margin: 80px 60px;
}

.personQuote {
    display: grid;
    box-sizing: border-box;
    grid-template-columns: 32px 1fr 32px;
    grid-template-rows: 32px 1fr 32px;
    align-items: center;
    background-color: rgba(235, 186, 185, 0.3);
    box-shadow: 2px 2px 5px grey;
    border-radius: 3px;
    width: 320px;
    margin: 10px 0;
    padding: 5px 10px;
    text-align: center;
}

.personQuote p {
    font-size: 18px;
    font-weight: 400;
    font-style: italic;
    grid-column: 1/4;
    grid-row: 2;
}

.personQuote i {
    font-size: 20px;
    color: rgba(8, 65, 92,0.9);
}

.personRightQuote {
    grid-column: 3;
    grid-row: 3;
    justify-self: end;
}


@media only screen and (min-width: 601px) and (max-width: 900px){
    .person {
        grid-template-columns: 300px 270px;
    }
    .personQuote {
        grid-column: 1 / 3;
    }
}

@media only screen and (max-width: 600px){
    .person {
        padding: 20px 0 0 0;
        grid-template-columns: 320px;
    }
    .personQuote {
        margin: 10px 0 20px 0;
    }
    .personText {
        margin-top: 0;
    }
}
.fixedOverlay {
    position: fixed;
    background-color: rgba(180, 180, 180, 0.8);
    height: 100vh;
    width: 100vw;
    padding-top: 70px;
    text-align: end;
}

.foCloser {
    margin: 20px 50px 20px 0;
    color: white;
    opacity: 0.8;
    cursor: pointer;
}

.foHidden {
    display: none;
}

.foVisible {
    display: block;
}

.foContent {
    max-width: 90%;
    max-height: 90vh;
    margin: 0 auto;
}

.foContent img {
    display: block;
    max-width: 80vw;
    max-height: 80vh;
    margin: 0 auto;
}
